import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UsuariosService } from '../services/usuarios.service';
import { QuestionarioService } from '../services/questionario.service';
import { SessaoService } from '../services/sessao.service';
import { ModalController } from '@ionic/angular';
import { PacienteEntendendoFumoComponent } from '../components/paciente-entendendo-fumo/paciente-entendendo-fumo.component';
import { PacienteResponderDiaComponent } from '../components/paciente-responder-dia/paciente-responder-dia.component';
import { PacienteResponderSessaoComponent } from '../components/paciente-responder-sessao/paciente-responder-sessao.component';
import { PacienteService } from '../services/paciente.service';
import { Paciente } from '../models/paciente';

@Component({
  selector: 'app-paciente',
  templateUrl: './paciente.page.html',
  styleUrls: ['./paciente.page.scss'],
})
/**
 * Tela principal do Paciente
 */
export class PacientePage {

  /** Exibe o titulo da página */
  page: {titulo:string, color:string} = {titulo: 'Perfil', color:'warning2'};
  paciente:Paciente = null;
  constructor(private router:Router, private usuariosService:UsuariosService, private questionarioService:QuestionarioService, private sessaoService:SessaoService, private modalController:ModalController, private pacienteService:PacienteService) { }

  async ionViewWillEnter () {
    this.paciente = null;
    // await new Promise(resolve => setTimeout(resolve, 2000));
    this.paciente = await this.pacienteService.getPacienteLogado();
    
    //Questionários Diário
    let dias = await this.questionarioService.buscarQuestionariosAbertos();
    if (dias.length > 0) {
      let modal = await this.modalController.create({
        component:PacienteResponderDiaComponent,
        componentProps: {dias, sair: this.sair.bind(this)},
        backdropDismiss: false
      });
      await modal.present();
    }
    
    //Sessoes
    let sessoes = await this.sessaoService.buscarSessoesResponder(this.paciente);
    sessoes.forEach(async (sessao) => {
      let modal = await this.modalController.create({
        component:PacienteResponderSessaoComponent,
        componentProps: {sessao},
        backdropDismiss: false
      });
      await modal.present();
    })

    if (this.paciente.primeiroAcesso) {
      //Primeiro Acesso
      const modalCadastro = await this.modalController.create({
        component:PacienteEntendendoFumoComponent,
        backdropDismiss: false,
        componentProps: {paciente:this.paciente},
      });
      modalCadastro.present();
    }

  }

  /**
   * Informa o nome do titulo a ser exibido na página 
   * @param titulo Titulo a ser exibido na página
   */
  setTitulo(page:string) {
    if (page == 'perfil') this.page = {titulo: 'Perfil', color:'warning2'};
    if (page == 'conquistas') this.page = {titulo: 'Conquistas', color:'success'};
    if (page == 'financeiro') this.page = {titulo: 'Financeiro', color:'danger'};
    if (page == 'chat') this.page = {titulo: 'Chat', color:'primary'};
    
    
  }

  /**
   * Desloga o usuário do sitema
   */
  sair() {
    this.usuariosService.deslogar();
    //window.location.reload();
    this.router.navigateByUrl('/login');
  }


}
