import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';

import { IonicModule } from '@ionic/angular';

import { PacientePage } from './paciente.page';
import { ComponentsModule } from '../components/components.module';

const routes: Routes = [
  {
    path: '',
    component: PacientePage,
    children: [
      { path: '', redirectTo: 'perfil' },
      { path: 'perfil', loadChildren: './perfil/perfil.module#PerfilPageModule' },
      { path: 'financeiro', loadChildren: './financeiro/financeiro.module#FinanceiroPageModule' },
      { path: 'conquistas', loadChildren: './conquistas/conquistas.module#ConquistasPageModule' },
      { path: 'chat', loadChildren: './chat/chat.module#ChatPageModule' }
    ]
  }
];

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    IonicModule,
    ComponentsModule,
    RouterModule.forChild(routes)
  ],
  declarations: [PacientePage]
})
export class PacientePageModule {}
